
.foot{
  position: fixed;
  left: 0;
  bottom: 120px;
  width: 100%;
  height: 60px;
  background-color: rgba(0, 0, 0, 1);
  color: #fff;
  z-index: 99;
}
.nav{
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.nav li{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 100%;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  vertical-align: middle;
}
.nav li a{
  text-decoration: none;
  display: block;
  height: 60px;
  line-height: 60px;
}



